<style lang="less">
.settingWrap{
  width: 100%;
  height: 100vh;
  background-color: #f2f2f2;
  .settingTitle {
    color: #000;
    font-size: 13px;
    font-weight: bold;
    padding: 5rpx 0 5rpx 20rpx;
    box-sizing: border-box;
    background-color: #ddd;
  }
  .deviceName {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 12px;
    height: 60rpx;
    padding: 0 20rpx;
    border-bottom: 1px solid #ddd;
    .moreIcon {
      display: flex;
      align-items: center;
      .moreImg {
        width: 30rpx;
        margin-left: 10rpx;
        
      }
    }
    .deviceRecord {
      margin-right: -20rpx;
      transform: scale(0.6);
    }
  }
}
</style>
<template>
  <view class="settingWrap">
    <view class="settingVideo">
      <view class="settingTitle">视频设置</view>
      <!-- 设备重命名 -->
      <view class="deviceName">
        <view>设备重命名</view>
        <view class="moreIcon" @click="renameDeviceEvent(deviceName)">
          <view>{{deviceName}}</view>
          <image class="moreImg" src="../../static/mine/mine-more.png" mode="widthFix"></image>
        </view>
      </view>
      <!-- 设置画质 -->
      <view class="deviceName">
        <view>画质</view>
        <view class="moreIcon">
         <picker :range="picQualityArr" :value="index" @change="settingPicQualityEvent">
           <view>{{picQualityArr[index]}}</view>
         </picker>
          <image class="moreImg" src="../../static/mine/mine-more.png" mode="widthFix"></image>
        </view>
      </view>
      <!-- 录像文件时长 -->
      <view class="deviceName">
        <view>录像文件时长</view>
        <view class="moreIcon">
          <picker :range="videotapeTimerArr" :value="indexTimer" @change="videotapeTimerEvent">
            <view>{{videotapeTimerArr[indexTimer]}}</view>
          </picker>
          <image class="moreImg" src="../../static/mine/mine-more.png" mode="widthFix"></image>
        </view>
      </view>
      <!-- 录音 -->
      <view class="deviceName">
        <view>录音</view>
        <view class="moreIcon deviceRecord">
          <switch checked="isChecked" @change="recordSoundEvent" color="#DC851F" />
        </view>
      </view>
      <!-- 记录仪喇叭音量 -->
      <view class="deviceName">
        <view>记录仪喇叭音量</view>
        <view class="moreIcon">
          <picker :range="recordHornSoundArr" :value="indexSound" @change="recordHornSoundEvent">
            <view>{{recordHornSoundArr[indexSound]}}</view>
          </picker>
          <image class="moreImg" src="../../static/mine/mine-more.png" mode="widthFix"></image>
        </view>
      </view>
    </view>
    <!-- 系统设置 -->
    <view class="settingVideo">
      <view class="settingTitle">系统设置</view>
      <!-- 修改 wifi 密码 -->
      <view class="deviceName"  @click="toSkipModifyPwd">
        <view>修改wifi密码</view>
        <view class="moreIcon">
          <image class="moreImg" src="../../static/mine/mine-more.png" mode="widthFix"></image>
        </view>
      </view>
      <!-- 记录仪系统升级 -->
      <view class="deviceName" @click="systemUpgradeEvent">
        <view>记录仪设备升级</view>
        <view class="moreIcon">
          <view>V1.0.0</view>
          <image class="moreImg" src="../../static/mine/mine-more.png" mode="widthFix"></image>
        </view>
      </view>
      <!-- 格式化TF卡 -->
      <view class="deviceName" @click="clearTFCartEvent">
        <view>格式化TF卡</view>
        <view class="moreIcon">
          <image class="moreImg" src="../../static/mine/mine-more.png" mode="widthFix"></image>
        </view>
      </view>
      <!-- 恢复出厂设置 -->
      <view class="deviceName" @click="restoreSetingEvent">
        <view>恢复出厂设置</view>
        <view class="moreIcon">
          <image class="moreImg" src="../../static/mine/mine-more.png" mode="widthFix"></image>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        deviceName: "IPC-IMX307", // 设备名
        picQualityArr: ['1080P,25FPS', '1440P,25FPS'],
        index: 0, //画质索引
        videotapeTimerArr: ["1分钟", "3分钟", "5分钟"],
        indexTimer: 0, // 录像索引
        isChecked: false, // 录音是否开启
        recordHornSoundArr: ["关", "低", "中", "高"], // 记录仪喇叭音量
        indexSound: 0 // 记录仪喇叭音量
      }
    },
    onShow () {
      uni.$on("getRenameDevice", (val) => {
        this.deviceName = val
      })
    },
    methods: {
      
      // 设备重命名事件
      renameDeviceEvent (deviceName) {
        this.$uniapi.goGavigate(`../rename/rename?deviceName=${deviceName}`)
      },
      // 设置画质
      settingPicQualityEvent (e) {
        this.index = e.target.value
        // 发送请求
        
      },
      // 录像时长设置
      videotapeTimerEvent (e) {
        this.indexTimer = e.target.value
        // 发送请求
        
      },
      // 录音事件
      recordSoundEvent (e) {
        this.isChecked = e.target.value
        // 发送请求
        
      },
      // 记录仪喇叭音量
      recordHornSoundEvent (e) {
        this.indexSound = e.target.value
        // 发送请求
        
      },
      // 跳转到修改密码页面
      toSkipModifyPwd () {
        this.$uniapi.goGavigate('../password/password')
      },
      // 记录仪系统升级
      systemUpgradeEvent () {
        
      },
      // 格式化IF卡
      clearTFCartEvent () {
        
      },
      // 恢复出厂设置
      restoreSetingEvent () {
        
      }
    }
  }
</script>

